$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

body {
	overflow-x: hidden;
	// overflow-y: hidden;
}



// Product Swiper Directory
.bpwpsb-product-swiper,
.wp-block-bpwpsb-product-swiper {
	* {
		box-sizing: border-box;
	}

	.preloaderIcon {
		text-align: center;
	}

	.cardContainer {
		cursor: pointer;
		position: relative;
		aspect-ratio: 1/1;

		// overflow-y: hidden;
		// height: 100%;

		.swipe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			.card {
				position: relative;
				margin: 0;
				padding: 0;
				box-shadow: none;
				border: none;
				max-width: 100%;
				height: 100%;

				.main {
					position: absolute;
					width: 100%;
					height: 100%;
					bottom: 10%;
					overflow: hidden;

					.title,
					.desc {
						position: absolute;
						left: 50%;
						bottom: 2%;
						z-index: 999;
						border: 1px solid #00000030;
						margin: 0;
						transform: translate(-50%);
						padding: 2px 5px;
						font-size: 14px;
						background: #fff;
						line-height: 135%;
						-webkit-transition: 0.7s ease-out;
						-moz-transition: 0.7s ease-out;
						-o-transition: 0.7s ease-out;
						transition: 0.7s ease-out;

						@media (max-width: 767px) {
							bottom: 5%;
						}

						@media (max-width: 575px) {
							bottom: 10%;
						}

						&.title {
							// left: 40%;
						}

						&.hidden {
							-webkit-transition: 1s ease-out;
							-moz-transition: 1s ease-out;
							-o-transition: 1s ease-out;
							transition: 1s ease-out;
							bottom: -100%;
						}
					}
				}

				.galleryArea {
					position: relative;
					height: 100%;

					.galleryImages {
						height: 100%;
						max-width: 100%;
						width: 100%;
						background-size: cover;
						background-position: center;
						background-repeat: no-repeat;

						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
						}
					}

					.leftIcon,
					.rightIcon {
						cursor: pointer;
					}

					.icon {
						display: flex;
						align-items: center;
						height: 100%;
					}

					.leftIcon {
						position: absolute;
						top: 0;
						left: 0;
						height: 100%;
						width: 50%;
						display: none;

						@media (max-width: 991px) {
							display: block;
						}

						.icon {
							margin-left: 10px;
						}
					}

					.rightIcon {
						position: absolute;
						top: 0;
						right: 0;
						width: 50%;
						height: 100%;
						display: none;

						@media (max-width: 991px) {
							display: block;
						}

						.icon {
							justify-content: end;
							margin-right: 10px;
						}
					}

					.buttonArea {
						position: absolute;
						top: -4px;
						width: 97%;
						display: flex;
						align-items: center;
						justify-content: center;
						gap: 3px;
						left: 50%;
						transform: translate(-50%, 50%);
						z-index: 9999;


						.btnIndicator {
							display: inline-flex;
							width: 100%;
							padding: 3px 0px;

							button {
								width: 100%;
								border: none;
								background-color: #000;
								border-radius: 3px;
								height: 5px;
								padding: 0px;
								margin: 0px;
								cursor: pointer;
							}

							.active {
								background-color: #fff;
							}
						}
					}
				}

				&:hover {

					.galleryArea .leftIcon,
					.galleryArea .rightIcon {
						display: block;
					}
				}
			}
		}

		.actionBtn {
			position: absolute;
			bottom: 0;
			width: 100%;

			.btnList {
				display: flex;
				align-items: center;
				justify-content: center;
				gap: 6px;

				a {
					text-decoration: none;
					padding: 0;
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 2px;
					background: transparent;
					cursor: pointer;
					color: #000;
					font-size: 16px;
					font-weight: 500;

					@media (max-width: 576px) {
						width: 45px;
						height: 45px;
					}
				}

				.productExit {
					background-color: #00000026;
				}
			}
		}
	}

	.msg {
		h3 {
			font-size: 28px;
			margin: 0;
			border: 2px solid #000;
			padding: 10px;
			font-weight: 600;
			color: #000;

		}
	}

	.grid {
		display: grid;

		.single_item {
			text-align: center;

			.img {
				width: 100%;
				// height: 100%;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			.name {
				h2 {
					font-size: 18px;
					font-weight: 500;
					margin: 0;
				}
			}

			.price_area {
				.discount_price {
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 10px;


					.regular_price {
						font-size: 14px;
						font-weight: 400;
						text-decoration: line-through;
					}

					.price {
						font-size: 14px;
						font-weight: 400;
					}
				}


				.sale_price {
					font-size: 14px;
					font-weight: 400;
				}
			}

			.cart_btn {
				margin-top: 10px;

				button {
					&:focus {
						outline: none;
					}

					font-size: 16px;
					padding: 5px 8px;
					background: #fff;
					border: 1px solid #0000005c;
					cursor: pointer;
				}
			}
		}
	}
}

.entry-content {
	overflow: hidden;
}



/*
*
*column set
*/
@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@keyframes mymove {
	from {
		left: 0px;
	}

	to {
		left: 200px;
	}
}